<template>
  <div>
    <h1>外部短信推送</h1>
    <a-divider />
    <div class="row a-row flex-center">
      <div id="add" class="col-md-18">
        <div class="flex-start">

          <a-modal title="确认发送" :visible="modalVisible" @ok="submit" :confirmLoading="loading" @cancel="handleCancel">
            <p>确认发送短信</p>
          </a-modal>
          <!-- 短信模板选择 -->
          <a-form @submit="showModal" :autoFormCreate="(form)=>{this.form = form}">
            <!-- 模板选择 -->
            <a-form-item label='短信模板' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" fieldDecoratorId="smsTemplateChosen" :fieldDecoratorOptions="{rules: [{ required: true, message: '请选择短信模板' }]}">
              <a-select placeholder='Select a option and change input text above' @change="this.updateTemplate">
                <a-select-option :value="item.id" :key="item.id" v-for="item in SMSTemplates">{{item.text}}</a-select-option>
              </a-select>
            </a-form-item>

            <a-divider v-if="templateContent != ''" orientation="left">短信模板</a-divider>
            <a-row v-if="templateContent != ''">
              <a-col :span="5"></a-col>
              <a-col :span="12">
                <a-card :no-hover="true" style="margin: auto;">
                  <div style="min-height:20px">
                    {{ templateContent }}
                  </div>
                </a-card>

              </a-col>
              <a-col :span="7"></a-col>
            </a-row>
            <br>

            <!-- 填充内容 -->
            <a-form-item v-for="item in fillIns" :key="item.id" :label='"字段"+"{"+item.id+"}"' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" :fieldDecoratorId="item.id" :fieldDecoratorOptions="{rules: [{ message: 'Please input your note!' }]}">
              <!-- <a-input :value="item.text" /> -->
              <a-input v-model="item.text" />
            </a-form-item>

            <!-- 发送范围选择 -->
            <a-divider orientation="left">发送范围</a-divider>
            <!-- 短信范围 -->
            <a-form-item label='发送手机号' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }" fieldDecoratorId="smsPhoneListChosen" :fieldDecoratorOptions="{rules: [{ required:  false, message: '请选择发送组别' }]}">
              <a-select mode="tags" placeholder="逗号分隔手机号" :tokenSeparators="[',']">
                <a-select-option v-for="i in 0" :key="i.toString(1)">{{i}}</a-select-option>
              </a-select>
            </a-form-item>

            <a-form-item :wrapperCol="{ span: 12, offset: 5 }">
              <a-button :loading="loading" type='primary' htmlType='submit'>
                提交
              </a-button>
            </a-form-item>
          </a-form>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})
export default {
  name: 'outerSMS',
  methods: {
    updateTemplate: function (value) {
      if (this.SMSTemplates.length > 0) {
        this.SMSTemplates.map(function (n) {
          if (n.id === value) {
            this.templateContent = n.text
          }
        }.bind(this))
      }
      this.fillIns = []

      /* eslint-disable */
      let fillsData = this.templateContent.match(/[^{\}]+(?=})/g)
      if (fillsData != null && fillsData.length >= 1) {
        this.fillIns = fillsData.map(function (e) { return { id: e, text: '' } })}
      else{
        this.fillIns = []
      }
    },
    handleSMSGroupChange(value) {
      this.PhoneList = value
    },
    handleSMSReciverChange(memberChosen, direction, moveKeys) {
      this.memberChosen = memberChosen
    },
    handleSMSReciverSelectChange(sourceSelectedKeys, targetSelectedKeys) {
      this.selectedKeys = [...sourceSelectedKeys, ...targetSelectedKeys]
    },
    showModal() {
      this.modalVisible = true
    },
    handleCancel: function(e) {
      this.$message.info('已取消')
      this.modalVisible = false
    },
    submit: function (e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.loading = true
          service
            .post( '/open/sms',
              {
                template: values.smsTemplateChosen,
                param_list: this.fillIns.map(function (n) {
                  return n.text
                }),
                phone_list: values.smsPhoneListChosen
              }
            )
            .then(function (response) {
              this.$message.success('发送成功')
            }.bind(this))
            .catch(function (error) {
              this.$message.error(error.response.data.message)
            }.bind(this))
            .then(function () {
              this.loading = false
            }.bind(this)
            )
        }
      })
      this.modalVisible = false
    },
    cancel: function(e){
      this.message.info('已取消')
      this.modalVisible = false
    }
  },

  data () {
    return {
      PhoneList:[ ],
      groupChosen: [ ],
      memberChosen: [ ],
      SMSTemplateChosen: 0,
      groupList: [ ],
      memberList: [ ],
      fillIns: [ ],
      SMSTemplates: [ ],
      templateContent: '',
      submitLoading: false,
      loading:false,
      modalVisible: false,
    }
  },
  mounted: function () {
    service
      .get('/weixin/department', { timeout: 4000 })
      .then(function (response) {
        this.groupList = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$message.error(error.response.data.msg)
      }.bind(this)
      )
    service
      .get('/weixin/department/1', { timeout: 4000 })
      .then(function (response) {
        this.memberList = response.data.data
        this.memberList.forEach((item) => {
          item.key = item.userid
          item.title = item.name
        })
      }.bind(this))
      .catch(function (error) {
        this.$message.error(error.response.data.msg)
      }.bind(this))
    service
      .get('/message/sms/template', { timeout: 4000 })
      .then(function (response) {
        this.SMSTemplates = response.data.data
      }.bind(this))
      .catch(function (error) {
        this.$message.error(error.response.data.msg)
      }.bind(this)
      )
  }
}
</script>

<style>
</style>



